<template>
    <div class="form-input">
        <label :for="inputName" class="material" :class="{ focused: focused }">{{ label }}</label>
        <template v-if="!multiline">
            <input
              type="text"
              :id="inputName"
              :name="inputName"
              :placeholder="placeholder"
              class="material"
              :class="{ focused: focused }"
              @click="focused = true"
              @blur="focused = false"
              @focus="focused = true" />
        </template>
        <template v-if="multiline">
            <textarea
              :id="inputName"
              :name="inputName"
              :placeholder="placeholder"
              :rows="lines"
              class="material"
              :class="{ focused: focused }"
              @click="focused = true"
              @blur="focused = false"
              @focus="focused = true"></textarea>
        </template>
    </div>
</template>

<script>
export default {
  name: 'material-input',
  props: {
    inputName: {
      type: String
    },
    label: {
      type: String
    },
    multiline: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String
    },
    lines: {
      type: Number
    }
  },
  data: function () {
    return {
      focused: false
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .form-input {
        display: flex;
        flex-direction: column;
        width: 75%;
        text-align: left;
        margin-left: auto;
        margin-right: auto;
        margin-top: 15px;

        input, textarea {
            &.material {
                border: 0;
                border-bottom: 1px solid lightgray;
                outline: none;
            }

            &.focused {
                border-bottom: 1px solid $secondaryColour;
            }
        }

        label {
            position: relative;
            cursor: text;
            transition: top 0.2s ease 0s;

            &.material {
                top: $lineHeight;
            }

            &.focused {
                top: 0;
            }
        }
    }
</style>



